.o-cp {
  @apply m-0 flex min-w-full min-h-screen;
  background-color: var(--ls-color-light);
}
.o-cp__sidebar {
  @apply w-3/12 shadow-sm;
  max-width: 300px;
}
.o-cp__topbar {
  @apply w-full shadow-sm h-20;
}
.o-cp__main {
  @apply flex-grow flex flex-col;
}
.o-cp__content {
  @apply flex-grow;
}

.c-topbar {
  @apply m-0 h-full w-full flex items-center;
  color: var(--ls-color-dark);
  background-color: var(--ls-color-white);
  padding: 0 calc(var(--ls-spacing)*1rem);
}
.c-topbar__buttons {
  @apply flex-grow;
}

.c-sidebar {
  @apply m-0 p-0 flex flex-col min-w-full min-h-screen text-white bg-primary;
}
.c-sidebar__brand {
  @apply flex items-center justify-start h-20 p-8;
}
.c-sidebar__brand a {
  @apply text-2xl text-white;
}
.c-sidebar__nav {
  @apply flex-grow p-4;
}
.c-sidebar__nav nav > a {
  @apply block p-4 rounded-lg;
}
.c-sidebar__nav nav > a.-active {
  @apply font-bold;
  background-color: #00000015;
}
.c-sidebar__nav nav ul {
  @apply mb-4;
}
.c-sidebar__nav nav ul li {
  @apply list-disc ml-4;
}
.c-sidebar__nav nav ul li a {
  @apply block p-1 pl-0;
}
.c-sidebar__nav nav ul li a.-active {
  @apply font-bold;
}

.c-card {
  @apply m-4 bg-white rounded-lg;
}
.c-card__content {
  @apply p-4;
}

.c-form__fields {
  @apply flex flex-wrap;
}
.c-form__field {
  @apply w-full my-2;
}
.c-form__controls {
  @apply flex items-center;
}
.c-form__controls > nav:first-of-type {
  @apply flex-grow mr-2;
}

.c-field > label {
  @apply font-bold mb-2 inline-block;
}

.c-field__required {
  color: var(--color-red);
}

.c-table table {
  min-width: 100%;
}
.c-table table thead tr th {
  @apply px-8 py-4 text-left font-medium;
  background-color: rgba(0, 0, 0, 0.1);
}
.c-table table tbody tr {
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}
.c-table table tbody tr:hover {
  @apply shadow-sm;
  border: 1px solid rgba(0, 0, 0, 0.05);
  background-color: rgba(0, 0, 0, 0.025);
}
.c-table table tbody tr td {
  @apply px-8 py-4 whitespace-nowrap;
}
.c-table__content {
  @apply shadow-sm m-4 bg-white rounded-lg overflow-hidden;
}
.c-table__buttons .a-button {
  @apply font-medium px-2 py-1;
}
.c-table__actions, .c-table__limiter, .c-table__meta {
  @apply px-8 py-4 whitespace-nowrap;
}

.c-buttons {
  display: flex;
}

.a-button {
  @apply font-bold inline-block;
  color: var(--ls-color-white);
  background-color: var(--ls-color-primary);
  border-radius: calc(var(--ls-radius)*.5rem);
  padding: calc(var(--ls-spacing)*0.35rem) calc(var(--ls-spacing)*0.5rem);
}
.a-button span {
  color: var(--ls-color-white);
}

.c-shortcuts {
  display: flex;
  align-items: center;
  margin-left: calc(var(--ls-spacing)*.5rem);
}
.c-shortcuts > div {
  position: relative;
}
.c-shortcuts > div > a,
.c-shortcuts > div > button {
  padding: 0;
  border: none;
  display: flex;
  background: none;
  align-items: center;
  border-radius: 999px;
  justify-content: center;
  height: calc(var(--ls-spacing)*1.5rem);
  width: calc(var(--ls-spacing)*1.5rem);
  border: 2px solid transparent;
  outline: none;
}
.c-shortcuts > div > a:active, .c-shortcuts > div > a:focus,
.c-shortcuts > div > button:active,
.c-shortcuts > div > button:focus {
  border-color: var(--ls-color-primary);
}
.c-shortcuts > div > a svg,
.c-shortcuts > div > a img,
.c-shortcuts > div > button svg,
.c-shortcuts > div > button img {
  width: calc(var(--ls-spacing)*1.25rem);
  height: calc(var(--ls-spacing)*1.25rem);
}
.c-shortcuts > div > a svg,
.c-shortcuts > div > button svg {
  color: var(--topbar-text-color);
}
.c-shortcuts > div > a img,
.c-shortcuts > div > button img {
  border-radius: 999px;
}

.c-dropdown__content {
  @apply hidden;
}
.c-dropdown.-open__content {
  @apply block;
}

.c-input input[type=text],
.c-input input[type=password],
.c-input input[type=email],
.c-input input[type=number],
.c-input input[type=url],
.c-input input[type=date],
.c-input input[type=datetime-local],
.c-input input[type=month],
.c-input input[type=week],
.c-input input[type=time],
.c-input input[type=search],
.c-input input[type=tel],
.c-input textarea {
  color: var(--ls-color-dark);
  background-color: var(--ls-color-light);
  border-radius: calc(var(--ls-radius)*.5rem);
  padding: calc(var(--ls-spacing)*0.25rem) calc(var(--ls-spacing)*0.35rem);
}
.ls-input.--toggle-input input[type=checkbox] + .toggle {
  width: 60px;
  height: 32px;
  padding: 4px;
  border-radius: 42px;
  background: #B7BABF;
  box-sizing: border-box;
  transition: background 0.5s ease;
}
.ls-input.--toggle-input input[type=checkbox] + .toggle .toggle-pill {
  display: block;
  width: 24px;
  height: 24px;
  background: white;
  border-radius: 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  margin-left: 0;
  transition: margin 0.08s cubic-bezier(0.77, 0.49, 1, 1);
}

.ls-input.--toggle-input input[type=checkbox]:checked + .toggle {
  background: #3076F6;
  transition: background 0.5s ease;
}
.ls-input.--toggle-input input[type=checkbox]:checked + .toggle .toggle-pill {
  margin-left: calc(100% - 24px);
}

.ls-input.--toggle-input input[type=checkbox]:focus + .toggle {
  background: #3076F6;
}

.ls-input.--toggle-input input[type=checkbox] {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
}